/* 李萨如图参数显示 */
.mode-notice {
  background-color: #f0f8ff;
  padding: 8px 12px;
  margin: 5px 0 15px 0;
  border-radius: 4px;
  border-left: 3px solid #42b983;
}

.lissajous-params-display {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}

.lissajous-params-display strong {
  color: #42b983;
  font-size: 15px;
}

.simplified-ratio {
  font-size: 12px;
  color: #888;
  margin-left: 5px;
}

/* 箭头按钮样式 */
.arrow-button {
  font-size: 16px;
  font-weight: bold;
  padding: 4px 8px;
  min-width: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* 添加悬停效果 */
.arrow-button:hover {
  background-color: #1d5c82;
  transform: scale(1.05);
  transition: all 0.2s ease;
}

/* 初相差控制样式 */
.phase-control {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 5px;
}

.phase-control input[type="number"] {
  width: 60px;
  text-align: center;
}

.phase-control .arrow-button {
  padding: 4px 8px;
  min-width: 30px;
}

/* 初相差控制区域样式 */
.phase-diff-control {
  margin: 15px 0;
  padding: 15px;
  background: white;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  margin-bottom: 15px;
}

.phase-diff-control h3 {
  color: #2c3e50;
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 16px;
}

.phase-control-wrapper {
  display: flex;
  align-items: center;
  gap: 15px;
}

.phase-control-wrapper label {
  min-width: 80px;
  font-weight: 500;
}

.phase-control-wrapper .phase-control {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 200px;
}

/* 信号源设置区域样式 */
.signal-source-section {
  border: 2px solid #2b6d98;
  border-radius: 6px;
  padding: 15px;
  position: relative;
  margin-bottom: 20px;
  background-color: #f9fafb;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  border-bottom: 1px solid #2b6d98;
  padding-bottom: 8px;
}

.section-header h3 {
  margin: 0;
  color: #2b6d98;
  font-size: 18px;
}

.section-label {

  color: black;
  padding: 3px 8px;
  border-radius: 4px;
  font-size: 26px;
  font-weight: bold;

}

.signal-source-controls {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px dashed #ccc;
}

.wave-select-container h4,
.channel-control-container h4,
.parameter-control h4{
  margin-top: 0;
  margin-bottom: 10px;
  color: #2c3e50;
  font-size: 16px;
}

.wave-select {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

/* 调整表格样式，使其适合新的布局 */
.signal-source-section .parameter-control {
  margin-top: 15px;
}

.channel-color-block {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 2px;
  margin-right: 8px;
  vertical-align: middle;
}

/* 滑块行样式 */
.slider-row {
  /* 移除背景颜色 */
  transition: background-color 0.3s ease;
}

.slider-row:hover {
  background-color: #f9f9f9; /* 只在悬停时显示较浅的背景色 */
}

.slider-row td {
  padding: 10px 5px;
  text-align: center;
}

/* 控制面板响应式布局 */
@media screen and (max-width: 1200px) {
  main {
    flex-direction: column;
  }

  aside {
    width: 100%;
    max-width: none;
    max-height: none;
    height: auto;
  }

  .control-panel {
    max-width: 800px;
    margin: 0 auto;
  }

  /* 信号源控制响应式 */
  .signal-source-controls {
    justify-content: center;
  }

  /* 参数控制表格响应式 */
  .parameter-control table {
    max-width: 600px;
    margin: 0 auto;
  }
}

@media screen and (max-width: 768px) {
  .signal-source-controls {
    flex-direction: column;
    align-items: center;
  }

  .wave-select {
    justify-content: center;
  }

  .control-group {
    margin-left: 0;
    justify-content: center;
  }

  .reading-controls {
    margin-left: 0;
  }

  .slider-container {
    margin-left: 0;
    width: 100%;
    max-width: 300px;
  }

  /* 触发控制响应式 */
  .trigger-controls {
    flex-direction: column;
    align-items: center;
  }

  /* 位置控制响应式 */
  .position-controls {
    width: 100%;
    max-width: 200px;
    margin: 8px auto;
  }
}

/* 优化小屏幕显示 */
@media screen and (max-width: 480px) {
  .parameter-control th, 
  .parameter-control td {
    padding: 8px 4px;
    font-size: 14px;
  }

  input[type="number"] {
    width: 50px;
    font-size: 14px;
  }

  button {
    padding: 6px 10px;
    font-size: 14px;
  }
}
