___CSS_LOADER_EXPORT___.push([module.id, `.switcher{
  position: fixed;
  top: 12px;
  right: 12px;
  z-index: 9999;
  display: grid;
  grid-template-columns: var(--tile, 48px) var(--tile, 48px);
  column-gap: var(--switcher-gap, 0px);
  padding: 0 var(--switcher-extra, 0px);
  --icon-active: #42B983;
  --icon-inactive: #9aa3b2;
}

.switcher .tile{
  all: unset;
  cursor: pointer;
  width: var(--tile, 48px);
  height: var(--tile, 48px);
  display: grid;
  place-items: center;
  position: relative;
  background:
    repeating-linear-gradient(0deg, #1c1e23 0 2px, #15171b 2px 4px),
    repeating-linear-gradient(90deg, #14161a 0 8px, #121418 8px 16px);
  box-shadow:
    inset 0 0 0 1px rgba(255,255,255,.2),
    inset 0 0 0 2px rgba(255,255,255,.06);
}

.switcher .tile.right{
  box-shadow:
    inset 1px 0 0 #00000090,
    inset 2px 0 0 #ffffff10,
    inset 0 0 0 1px rgba(255,255,255,.2),
    inset 0 0 0 2px rgba(255,255,255,.06);
}

.switcher .tile:active{ transform: translateY(1px) }
.switcher .tile[aria-selected="false"]{ filter: brightness(.78) contrast(1.05) }
.switcher .tile[aria-selected="true"]{
  box-shadow: inset 0 0 0 1px #e7e9ec, inset 0 0 0 2px rgba(255,255,255,.2), 0 0 0 1px #ffffff20;
}

.switcher svg{ width: var(--icon, 24px); height: var(--icon, 24px); display: block }
.switcher .stroke{ stroke: var(--icon-inactive); stroke-width:1.8; fill:none; stroke-linecap:round; stroke-linejoin:round }
.switcher .dot{ fill: var(--icon-inactive) }
.switcher .tile[aria-selected="true"] .stroke{ stroke: var(--icon-active) }
.switcher .tile[aria-selected="true"] .dot{ fill: var(--icon-active) }
.switcher .tile:hover .stroke{ stroke: var(--icon-active) }
.switcher .tile:hover .dot{ fill: var(--icon-active) }

.sr-only{ position:absolute; width:1px; height:1px; margin:-1px; overflow:hidden; clip:rect(0,0,0,0); white-space:nowrap; border:0 }




